@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
:root {
  --font: 'Rubik', sans-serif;
  --primary-font-weight: 700;
  --secondary-font-weight: 400;

  --success-color: #57d159;
  --darksky-color: #002b7a;
  --lightsky-color: #0596ff;
  --white-color: #ffffff;

  --card-border-radius: 1.25rem;
  --card-shadow-03dp: 0px 3px 4px rgba(0, 0, 0, 0.14), 0px 3px 3px rgba(0, 0, 0, 0.12), 0px 1px 8px rgba(0, 0, 0, 0.2);
  --card-shadow-04dp: 0px 4px 5px rgba(0, 0, 0, 0.14), 0px 1px 10px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.2);
  --card-shadow-08dp: 0px 8px 10px rgba(0, 0, 0, 0.14), 0px 3px 14px rgba(0, 0, 0, 0.12), 0px 5px 5px rgba(0, 0, 0, 0.2);
}

* {
  font-family: 'Rubik', sans-serif !important;
  font-family: var(--font) !important;
  box-sizing: border-box;
}

header {
  color: var(--darksky-color);
  text-align: center;
}

header h1 {
  font-weight: var(--primary-font-weight);
  text-transform: uppercase;
  font-size: 4rem;
}

header p {
  font-weight: var(--secondary-font-weight);
  font-size: 1.3rem;
}

@media (max-width: 767px) {
  header h1 {
    font-size: 3rem;
  }
}

.score-display,
.score {
  display: flex;
  flex-direction: row;
}

.score-display {
  justify-content: center;
}

.score-display .score {
  margin: 1rem;

  font-weight: var(--primary-font-weight);
  text-transform: uppercase;
  color: var(--darksky-color);
  font-size: 2rem;
}

.score .score-value {
  color: var(--lightsky-color);
  margin-left: 0.5rem;
}

@media (max-width: 767px) {
  .score-display .score {
    font-size: 1.5rem;
  }
}

.message-card {
  background-color: var(--success-color);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 0.5rem;
  border-radius: var(--card-border-radius);
  box-shadow: var(--card-shadow-03dp);
}

.message-card-icon {
  height: 2.63rem;
  width: auto;
  margin-right: 1rem;
}

.message-card-text {
  font-size: 1.125rem;
  font-weight: var(--primary-font-weight);
  text-transform: uppercase;
  color: var(--white-color);
}

.button {
  padding: 0.5rem 2rem;
  min-height: 2.63rem;
  outline: none;
  background: none;
  border: none;
  background-color: var(--darksky-color);
  border-radius: var(--card-border-radius);
  font-size: 1.125rem;
  color: var(--white-color);
  text-transform: uppercase;
  font-weight: var(--primary-font-weight);
  box-shadow: var(--card-shadow-03dp);
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.button:hover {
  box-shadow: var(--card-shadow-04dp);
  background-color: var(--success-color);
}

.play-again {
  display: flex;
  justify-content: center;
}

.play-again > * {
  margin: 0 0.5rem;
}

.card {
  text-align: center;
  max-width: 18rem;
  min-width: 18rem;
  background-color: var(--darksky-color);
  margin: 1rem;
  border-radius: var(--card-border-radius);
  box-shadow: var(--card-shadow-04dp);
  cursor: pointer;
}

.card:nth-child(even) {
  background-color: var(--success-color);
}

.card img {
  height: 14.625rem;
  width: auto;
  max-width: 100%;
}

.card figcaption {
  border-bottom-right-radius: var(--card-border-radius);
  border-bottom-left-radius: var(--card-border-radius);
  font-size: 1.5rem;
  font-weight: var(--primary-font-weight);
  color: var(--darksky-color);
  background-color: var(--white-color);
  padding: 1rem 0;
}

.card:hover {
  box-shadow: var(--card-shadow-08dp);
}

.card:hover figcaption {
  color: var(--lightsky-color);
}

.card-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

footer {
  background-color: var(--darksky-color);
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

footer a {
  text-decoration: none;
  color: var(--white-color);
  font-size: 1.2rem;
}

footer a:last-child {
  margin-top: 1rem;
}

footer a:hover {
  color: var(--success-color);
}

